.log-in-section {
    height: 100vh; width: 100%;
    display: flex; flex-flow: column;
    align-items: center;
    padding-top: 40px;
    background-color: rgb(26, 29, 41);
}

.image-log-in {
    width: 160px; height: 90px;
}

.disney-logo-login {
    width: 100%; height: 100%;
}

.user-already-logged{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 33px;
    font-weight: 600;
}