.pdw-section-repeat {
    position: relative;
    width: 370px; height: 100%;
    display: flex; flex-flow: column;
    justify-content: space-evenly;
    transition: 350ms ease-in-out 0s;
    opacity: 1;
}

.pwd-repeat {
    position: relative;
}

.pwds-incorrect {
    position: absolute;
    width: 100%; border: 1px solid red;
    background-color: rgba(240, 54, 54, 0.54);
    border-radius: 5px;
    height: 40px;
    bottom: -50px;
    display: none; align-items: center;
    justify-content: center;
    font-size: 20px;
}

.error-email{
    opacity: 0;
    position: absolute;
    justify-self: center;
    background-color: rgba(224, 224, 1, 0.857);
    align-self: center;
    padding: 5px 20px;
    bottom: 0px;
    border: 2px solid rgb(189, 189, 0);
    border-radius: 5px;
}

.error-messages{
    position: absolute;
    bottom: 40px;
    color: white;
    font-size: 16px;
    display: flex;
    flex-flow: column;
    align-items: flex-start;
    max-width: 360px;
}

.li-error-msg{
    text-decoration: dotted;
    list-style: disc;
}

.no-errors-msg{
    display: none;
}

@media (max-width: 480px) {
    .error-email{
        padding: 5px 10px;
    }
}

@media (max-width: 400px) {
    .error-messages{
        font-size: 14px;
    }
}